@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;400;500;600;700&display=swap');
/* @import 'src\styles\style.scss'; */

body
{
    font: 18px "Nunito", sans-serif;
    color: black;
    min-height: 100vh;
    padding: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;

    &::before
    {
        content: '';
        position:fixed;
        inset: 0;
        z-index: -1;
        pointer-events: none;
        background: url("wallpaper.jpg") no-repeat center;
        background-size: cover;
        animation: backgroundZoomAnimate 20s forwards;
    }
}

@keyframes backgroundZoomAnimate
{
    from
    {
        transform: scale(1);
    }
    to
    {
        transform: scale(1.3);
    }
}

.profile-card {
    box-shadow: 0 18px 200px -60px black;
    border-radius: 50px;
    width: 650px;
    position: relative;
    backdrop-filter: blur(15px);
    border: 2px solid #ffffff40;
    padding: 3rem 5rem;
    display: flex;
    flex-direction: column;
    gap: 30px;
    
    
    /* text-align: center;
    flex-direction: colum;
    gap: 20px; */

  }
  @media screen and (max-width: 576px)  {
    .profile-card {
      width: auto;
    }
  }
 
  .profile-card img {
     margin: auto; 
    width: 200px;
    height: 200px;
    border-radius: 50%;
    object-fit: cover;
    display: block;
    box-shadow: 0 10px 60px -10px rgba(13, 28, 3, 0.5);
    
  }

  .profile-card_desc{
    text-align: center;
    /* display: block;  */
    flex-direction: colum;
    gap: 25px;
   }
  
  .profile-card_desc h1 {
    font-size: 2.5rem;
    font-weight: bold;
  }
  .profile-card_desc h1 + div {
    font-size: 20px;
    margin-bottom:17px;
  }
   

.profile-card_info {
    margin-top:35px;
    margin-bottom:35px;
  display: flex;
  justify-content: center;
    gap: 70px;
}

@media screen and (max-width: 576px) {
    .profile-card_info {
    gap: 30px;
  }
} 
 
.profile-card_info > div {
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 1px;
    text-align: center;
}
  
  .profile-card_info > div > *:first-child {
    font-weight: bold;
    letter-spacing: 3px;
    font-size:1.5rem;
  }  
  .profile-card_info > div > *:last-child {
    font-size: 16px;
    opacity: .7;
  }  

  .profile-card_social
  {
    margin-top:20px; 
    display: flex;
    justify-content:center;
    align-items: center;
    flex-wrap:wrap;
  }  
  .profile-card_social a {
   display: inline-flex;
   width:55px;
   height:55px;
   margin:15px;
   border-radius:40px;
   align-items: center;
   justify-content: center;
   color: white;
   position: relative;
   flex-shrink: 0;
   transition: all .2s;
  }
  @media screen and (max-width: 768px) {
    .profile-card_social a {
      width: 50px;
      height:50px;
      margin: 10px;
    }
  }
  @media screen and (min-width: 768px) {
    .profile-card_social :hover {
        transform: scale(1.015) translateY(-1px);
    }
  }
  .profile-card_social a.Instagram {
    background: linear-gradient(45deg,
    #405de6,
    #5851db,
    #833ab4,
    #c13584,
    #e1306c,
    #fd1d1d);
    box-shadow: 0 4px 30px rgba(120, 64, 190, 0.6);
  }

  .profile-card_social a.Youtube {
    background: linear-gradient(45deg, #fe2626,#e33b4e);
    box-shadow: 0 4px 30px rgba(43, 98, 169, 0.5);
  }
  .profile-card_social a.Github {
    background: linear-gradient(45deg, #333333,#626b73);
    box-shadow: 0 4px 30px rgba(63, 65,67, 0.6);
  }
  

  .profile-card_social_actions
  {
    display:flex;
    align-items: center;
    justify-content: center;
    gap:2rem;
  }

  @media screen and (max-width:576px)
  {.profile-card_social_actions
    {
        flex-direction:colum;
        gap: 1.5rem;
    }
  }

  .profile-card_social_actions > button{
    font-weight: bold;
    font-size: 22px;
    padding: 15px 0;
    min-width: 250px;
    border-radius: 50px;
    color: white;
    cursor: pointer;
    transition: all .2s;
  }

  @media screen and (max-width: 768px) {
    .profile-card_social_actions > button{
        min-width:170px;
    }
  }
  @media screen and (max-width: 576px) {
    .profile-card_social_actions > button{
        min-width:inherit;
        width:100%;
        max-width:350px;
    }
  }
  @media screen and (max-width: 576px) {
    .profile-card_social_actions > button :hover{
        transform: translateY(-5px);
    }
  }

  .profile-card_social_actions > button.blue
  {
    background: linear-gradient(45deg, #1da1f2, #0e71c8);
    box-shadow: 0 4px 30px rgba(19,127,212,0.4);
  }
  
  .profile-card_social_actions > button.blue :hover{
    box-shadow: 0 7px 130px rgba(19,127,212,1);
}

  .profile-card_social_actions > button.orange
  {
    background: linear-gradient(45deg, #d5135a, #f05924);
    box-shadow: 0 4px 30px rgba(233,45,70,0.35);
  }  
  
  .profile-card_social_actions > button.orange :hover{
    box-shadow: 0 7px 130px rgba(233,45,70,1);
}